import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'

const styles = {
  wrapperBox: {
    margin: '0 10px'
  },
  wrapper: {
    marginLeft: '10px'
  }
}

const Wrapper = props => {
  const { classes, box } = props
  return (
    <div className={box ? classes.wrapperBox : classes.wrapper}>
      {props.children}
    </div>
  )
}


Wrapper.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(Wrapper)
