import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { AppBar, Tabs, Tab, Typography, Button } from '@material-ui/core'
import SwipeableViews from 'react-swipeable-views'
import { inject, observer } from 'mobx-react'
import { Cards } from '../CardComponents'

const TabContainer = ({children, dir}) => {
  return (
    <Typography component="div" dir={dir} style={{ 
      padding: '10px',
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around'
     }}>
      {children}
    </Typography>
  )
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
  dir: PropTypes.string.isRequired,
}

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
  },
  AppBar: {
    marginTop: '12px',
    boxShadow: '0 1px 2px #d2d2d2',
    backgroundColor: '#fff'
  },
  button: {
    width: '100%',
    marginTop: '16px',
    height: '42px'
  },
})


@inject('homeStore')
@observer
class PersonTabs extends Component {

  state = {
    value: 0
  }

  componentDidMount() {
    this.fetchList()
  }
  fetchList(){
		this.props.homeStore.getSilderList()
  }
  
  handleChange = (event, value) => {
    this.setState({ value })
  }

  handleChangeIndex = index => {
    this.setState({ value: index })
  }

  render(){
    const { classes, theme } = this.props
    const { tabs } = this.props.homeStore
    return (
      <div className={classes.root}>
      <AppBar position="static" className={classes.AppBar}>
        <Tabs
          value={this.state.value}
          onChange={this.handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          scrollButtons="auto">
          <Tab label="创意" />
          <Tab label="灵感" />
          <Tab label="成就" />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={this.state.value}
        onChangeIndex={this.handleChangeIndex}
      >
        <TabContainer dir={theme.direction}>
        {
          tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
        }
          <Button variant="outlined" color="primary" className={classes.button}>
            查看更多
          </Button>
        </TabContainer>
        <TabContainer dir={theme.direction}>
        {
          tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
        }
          <Button variant="outlined" color="primary" className={classes.button}>
            查看更多
          </Button>
        </TabContainer>
        <TabContainer dir={theme.direction}>
        {
          tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
        }
          <Button variant="outlined" color="primary" className={classes.button}>
            查看更多
          </Button>
        </TabContainer>
      </SwipeableViews>
    </div>
    )
  }
}

PersonTabs.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
}

export default withStyles(styles, { withTheme: true })(PersonTabs)
