import classNames from 'classnames'
import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { capitalize } from '@material-ui/core/utils/helpers'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import ShareIcon from '@material-ui/icons/Share'
import weibo from '../../assets/svg/weibo.svg'
import weixin from '../../assets/svg/weixin.svg'
import qzone from '../../assets/svg/qzone.svg'

const styles = theme => ({
  root: {
    width: '100%',
    position: 'relative',
    height: 40
  },
  speedDial: {
    position: 'absolute',
    zIndex: 0,
    '&$directionRight': {
      top: '-8px',
      left: 0,
    }
  },
  fab:{
    color: 'red'
  },
  directionRight: {},
})

const actions = [
  { icon: <img src={weibo} alt="微博" />, name: '微博' },
  { icon: <img src={weixin} alt="朋友圈" />, name: '朋友圈' },
  { icon: <img src={qzone} alt="QQ空间" />, name: 'QQ空间' },
]

class Share extends React.Component {
  state = {
    direction: 'right',
    open: false,
  }

  handleClick = () => {
    this.setState(state => ({
      open: !state.open,
    }))
  }

  handleClose = () => {
    this.setState({ open: false })
  }

  handleOpen = () => {
    this.setState({ open: true })
  }

  render() {
    const { classes } = this.props
    const { direction, open } = this.state

    const speedDialClassName = classNames(
      classes.speedDial,
      classes[`direction${capitalize(direction)}`],
    )

    return (
      <div className={classes.root}>
        <SpeedDial
            ariaLabel="Share"
            className={speedDialClassName}
            icon={<ShareIcon />}
            onClick={this.handleClick}
            onClose={this.handleClose}
            open={open}
            direction={direction}
            >
        {actions.map(action => (
            <SpeedDialAction
            key={action.name}
            icon={action.icon}
            tooltipTitle={action.name}
            onClick={this.handleClick}
            />
        ))}
        </SpeedDial>
      </div>
    )
  }
}

Share.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Share)