import React, { Component } from 'react'
import { Typography } from '@material-ui/core'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import SwipeableViews from 'react-swipeable-views'
import { autoPlay } from 'react-swipeable-views-utils'
import { observer, inject } from 'mobx-react'
import Pagination from '../Pagination'
import Card from './Card'

const styles = {
  root: {
    width: '100%'
  },
  wrapper: {
    height: 326,
  },
  auto: {
    position: 'relative',
    height: '342px'
  }
}

const AutoPlaySwipeableViews = autoPlay(SwipeableViews)

const SwipeableContent = ({children, dir}) => {
    return (
      <Typography component="div" dir={dir} >
        {children}
      </Typography>
    )
  }

SwipeableContent.propTypes = {
    children: PropTypes.node.isRequired,
    dir: PropTypes.string.isRequired,
}

@inject('homeStore')
@observer
class StorySlider extends Component {
    state = {
        index: 0
    }
    handleChangeIndex = index => {
      this.setState({ index })
    }
  render() {
    const { classes, theme } = this.props
    const { story } = this.props.homeStore
    const { index } = this.state
    return (
      <div className={classes.auto}>
        <AutoPlaySwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={index}
            onChangeIndex={this.handleChangeIndex}
            slideStyle={styles.wrapper}
            resistance={true}
            interval={5000} >
            {story.map(item => {
              return (
                  <SwipeableContent dir={theme.direction} key={item._id}>
                    <Card data={item} />
                  </SwipeableContent>
                )
            })}
        </AutoPlaySwipeableViews>
        <Pagination dots={3} index={index} onChangeIndex={this.handleChangeIndex} />
      </div>
    )
  }
}

StorySlider.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
  }
  
export default withStyles(styles, { withTheme: true })(StorySlider)
  
