import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import BottomNavigation from '@material-ui/core/BottomNavigation'
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction'
import Person from '@material-ui/icons/Person'
import PlusOne from '@material-ui/icons/PlusOne'
import Palette from '@material-ui/icons/Palette'
import Add from '@material-ui/icons/Add'
import Email from '@material-ui/icons/Email'
import Fab from '@material-ui/core/Fab'
import { Link } from 'react-router-dom'

const styles = {
  root: {
    width: '100%',
    position: 'fixed',
    bottom: 0,
    zIndex: 99,
    boxShadow: '0px 0px 4px #e0e0e0'
  },
  item: {
    minWidth: '51px'
  },
  item1: {
    minWidth: '51px',
    height: '66px'
  },
  fabButton: {
    position: 'absolute',
    zIndex: 1,
    top: -24,
    left: 0,
    right: 0,
    margin: '0 auto',
    width: '50px',
    height: '50px'
  },
}
const Workshop = props => <Link to="/" {...props} />
const Inspiration = props => <Link to="/inspiration" {...props} />
const Release = props => <Link to="/release" {...props} />
const Message = props => <Link to="/message" {...props} />
const My = props => <Link to="/my" {...props} />

class BottomNav extends Component {

  render() {
    const { classes, selected } = this.props

    return (
      <BottomNavigation value={selected} className={classes.root} showLabels>
        <BottomNavigationAction className={classes.item} component={Workshop} label="创工坊" value="workshop" icon={<Palette />} />
        <BottomNavigationAction className={classes.item} component={Inspiration} label="灵感" value="inspiration" icon={<PlusOne />} />
        <BottomNavigationAction className={classes.item1} 
        component={Release} 
        label="发布" 
        value="release" 
        icon={<Fab color="primary" aria-label="Add" className={classes.fabButton}><Add /></Fab>} />
        <BottomNavigationAction className={classes.item} component={Message} label="私信" value="message" icon={<Email />} />
        <BottomNavigationAction className={classes.item} component={My} label="我的" value="my" icon={<Person />} />
      </BottomNavigation>
    )
  }
}

BottomNav.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(BottomNav)