import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { AppBar, Tabs, Tab, Typography, Button } from '@material-ui/core'
import SwipeableViews from 'react-swipeable-views'
import { Cards } from '../CardComponents'
import classnames from 'classnames'
import { inject, observer } from 'mobx-react'

const TabContainer = ({children, dir}) => {
  return (
    <Typography component="div" dir={dir} style={{ 
      padding: '10px',
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around'
     }}>
      {children}
    </Typography>
  )
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
  dir: PropTypes.string.isRequired,
}

const styles = theme => ({
  root: {
    backgroundColor: '#f9f9f9',
  },
  AppBar: {
    marginTop: '10px',
    backgroundColor: '#fff',
  },
  button: {
    width: '90%',
    margin: '16px 18px 0',
    height: '42px',
    borderRadius: '42px'
  },
  tips: {
    width: '100%',
    display: 'inline-block',
    textAlign: 'center',
    color: '#9c9c9c',
    fontSize: '12px',
  },
  fixedTabs: {
    position: 'fixed',
    top: 0,
    marginTop: 0,
  }
})


@inject('homeStore')
@observer
class TabsGroup extends Component {

  state = {
    value: 0
  }

  handleChange = (event, value) => {
    this.setState({ value })
  }

  handleChangeIndex = index => {
    this.setState({ value: index })
  }
  render(){
    const { theme, classes, fixed } = this.props
    const { tabs } = this.props.homeStore
    return (
      <div className={classes.root}>
      <AppBar position="static" 
      className={classnames([`${classes.AppBar}`, `card_shadow`].join(" "), {
        [classes.fixedTabs]: fixed,
      })}>
        <Tabs
          value={this.state.value}
          onChange={this.handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto">
          <Tab label="墨流" />
          <Tab label="软陶" />
          <Tab label="纸浆工艺" />
          <Tab label="写意画" />
          <Tab label="光影设计" />
          <Tab label="黑艺术" />
          <Tab label="概念设计" />
        </Tabs>
      </AppBar>
      <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        >
          <TabContainer dir={theme.direction}>
            {
              tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
            }
          </TabContainer>
          <TabContainer dir={theme.direction}>
          {
              tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
            }
          </TabContainer>
          <TabContainer dir={theme.direction}>
            {
              tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
            }
          </TabContainer>
          <TabContainer dir={theme.direction}>
            {
              tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
            }
          </TabContainer>
          <TabContainer dir={theme.direction}>
            {
              tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
            }
          </TabContainer>
          <TabContainer dir={theme.direction}>
            {
              tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
            }
          </TabContainer>
          <TabContainer dir={theme.direction}>
            {
              tabs.length > 0 && tabs[0].map((item, index) => <Cards key={index} data={item}/>)
            }
          </TabContainer>
      </SwipeableViews>
      <span className={classes.tips}>还没看够？快进入领域浏览更多吧</span>
      <Button variant="outlined" color="primary" className={classes.button}>进入领域</Button>
    </div>
    )
  }
}

TabsGroup.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
}

export default withStyles(styles, { withTheme: true })(TabsGroup)
