import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card, CardActionArea, CardMedia, CardContent } from '@material-ui/core'
import { Link } from 'react-router-dom'

const styles = {
  card: {
    height: '230px',
    width: '160px',
    borderRadius: '40px',
    margin: '10px 0'
  },
  media: {
    height: 147,
  }
}


const Cards = props => {
  const { classes } = props
  const { picture, author, info } = props.data
    return (
      <Card className={[`${classes.card}`, `card_shadow`].join(' ')}>
          <CardActionArea component={Link} to="/worksDetail">
              <CardMedia
              className={classes.media}
              image={picture}
              title={author} />
              <CardContent className={classes.info}>
                  <span>{author}</span>
                  <span>{info}</span>
              </CardContent>
          </CardActionArea>
      </Card>
  )
}

Cards.propTypes = {
    classes: PropTypes.object.isRequired
  }
  
export default withStyles(styles)(Cards)
  
