import React, { Component } from 'react'
import { Typography } from '@material-ui/core'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import SwipeableViews from 'react-swipeable-views'
import Card from './Card'
import avatar from '../../../assets/images/avatar.jpg'
import top1 from '../../../assets/images/top1.jpg'
import top from '../../../assets/images/top.png'

const styles = {
  root: {
    width: '100%'
  },
  wrapper: {
    width: '23%',
    height: 82,
  },
  slider: {
    marginTop: '16px',
    paddingLeft: '10px'
  }
}

const SwipeableContent = ({children, dir}) => {
    return (
      <Typography component="div" dir={dir} >
        {children}
      </Typography>
    )
  }

SwipeableContent.propTypes = {
    children: PropTypes.node.isRequired,
    dir: PropTypes.string.isRequired,
}

class CircleSlider extends Component {
    state = {
        value: 0
    }
    handleChangeIndex = index => {
      this.setState({ value: index })
    }
  render() {
    const { classes, theme } = this.props
    return (
        <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={this.state.value}
            onChangeIndex={this.handleChangeIndex}
            slideStyle={styles.wrapper}
            slideClassName={classes.slider} >
            <SwipeableContent dir={theme.direction}>
              <Card img={avatar}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={top}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={top1}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={avatar}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={top}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={top1}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={avatar}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={avatar}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={avatar}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={avatar}/>
            </SwipeableContent>
            <SwipeableContent dir={theme.direction}>
              <Card img={avatar}/>
            </SwipeableContent>
        </SwipeableViews>
    )
  }
}

CircleSlider.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
  }
  
export default withStyles(styles, { withTheme: true })(CircleSlider)
  
