import { homeServices } from '../services'
import {observable, computed, action, runInAction} from 'mobx'


class HomeStore {
    @observable.shallow _topSlider = []
    @observable.shallow _midSlider = []
    @observable.shallow _recommended = []
    @observable.shallow _story = []
    @observable.shallow _tabs = []
    @observable.shallow _tearch = []

    constructor (homeServices){
        this.service = homeServices
    }
    @computed get topSlider(){
        return [...this._topSlider]
    }
    @computed get midSlider(){
        return [...this._midSlider]
    }
    @computed get recommended(){
        return [...this._recommended]
    }
    @computed get story(){
        return [...this._story]
    }
    @computed get tearch(){
        return [...this._tearch]
    }
    @computed get tabs(){
        let tabsRes = []
        let data = [...this._tabs]
        for(var i = 0; i < data.length; i+=4){
            tabsRes.push(data.slice(i, i+4))
        }
        return tabsRes
    }
    @action getSilderList = async() => {
        try {
            const data = await this.service.fetchSliderData()
            runInAction(() => {
                this._topSlider = data.topSlider
                this._midSlider = data.midSlider
                this._recommended = data.recommended
                this._story = data.story
                this._tabs = data.tabs
                this._tearch = data.tearch
            })
        }catch(error){
            console.log(error)
        }
        
    }

}

export default new HomeStore(homeServices)