import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Card from '@material-ui/core/Card'
import TextField from '@material-ui/core/TextField'
import CardContent from '@material-ui/core/CardContent'
import Collapse from '@material-ui/core/Collapse'
import InputAdornment from '@material-ui/core/InputAdornment'
import Visibility from '@material-ui/icons/Visibility'
import VisibilityOff from '@material-ui/icons/VisibilityOff'
import Button from '@material-ui/core/Button'
import deepPurple from '@material-ui/core/colors/deepPurple'
import { Link } from "react-router-dom"
import ali from '../assets/svg/ali.svg'
import weichat from '../assets/svg/weichat.svg'
import login from '../assets/images/login.png'
import city from '../assets/images/city.png'

const styles = theme => ({
  card: {
    marginTop: '36%',
    margin: '16px',
    maxWidth: 400,
    boxShadow: '0 1px 10px #e6e4e4',
    borderRadius: 40
  },
  textField: {
    margin: '30px 16px',
    width: 300,
  },
  button: {
    width: '90%',
    margin: '0 16px',
    padding: '8px',
    color: theme.palette.getContrastText(deepPurple[500]),
    backgroundColor: '#3f51b5',
    borderRadius:  30,
    '&:hover': {
      backgroundColor: deepPurple[700],
    },
  },
  forgot: {
    width: '80px',
    display: 'flex',
    transform: 'translateX(165%)',
    color: '#3f51b5',
    padding: '10px 24px 16px',
    transition: 'all .2s ease-in-out',
    fontSize: '14px'
  },
  retrievePassword: {
    width: '80px',
    color: '#3f51b5',
    display: 'flex',
    padding: '12px 24px',
    transform: 'translateX(50%)',
    marginLeft: '56px',
    transition: 'all .2s ease-in-out',
    fontSize: '14px'
  },
  other_login: {
    display: 'flex',
    justifyContent: 'center',
    padding: '18px 0',
    color: '#969696',
    fontSize: 14,
  },
  other_icons: {
    display: 'flex',
    justifyContent: 'space-evenly',
    alignItems: 'center',
    padding: '20px 0',
  },
  other_icon: {
    height: '40px',
    width: '40px',
    textAlign: 'center',
    lineHeight: '54px'
  },
  login: {
    fontSize: 14,
    position: 'relative'
  },
  login_img: {
    position: 'absolute',
    top: 0,
    width: '60%',
    right: 0
  },
  login_img1: {
    position: 'fixed',
    bottom: 0,
    zIndex: -1
  }
})

const HomePage = props => <Link to="/" {...props} />

class Login extends Component {
  state = { 
    auth: '',
    phone: '',
    accout: '',
    password: '',
    expanded: false,
    showPassword: false,
  }
  
  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }))
  }

  handleChange = prop => event => {
    this.setState({ [prop]: event.target.value })
  }

  handleClickShowPassword = () => {
    this.setState(state => ({ showPassword: !state.showPassword }))
  }
  render() {
    const { classes } = this.props
    return (
      <div>
      <div className={classes.login_img}>
        <img src={login} alt=""/>
      </div>
       <Card className={classes.card}>
        <CardContent>
          <form noValidate autoComplete="off">
          <TextField
              id="standard-name"
              label="邮箱/手机号"
              value={this.state.accout}
              onChange={this.handleChange('accout')}
              className={classes.textField}
              type="text"
              margin="normal"
            />
            <TextField
              id="standard-password-input"
              label="密码"
              value={this.state.password}
              onChange={this.handleChange('password')}
              className={classes.textField}
              type={this.state.showPassword ? 'text' : 'password'}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end" onClick={this.handleClickShowPassword}>
                    {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                  </InputAdornment>
                ),
              }}
              margin="normal"
            />
            <Button variant="contained" color="primary" component={HomePage} className={classes.button}>
              登录
            </Button>
          </form>
        </CardContent>
        <span className={this.state.expanded ? classes.retrievePassword : classes.forgot}
         onClick={this.handleExpandClick}
        aria-expanded={this.state.expanded}>{this.state.expanded ? '找回密码' : '忘记密码？'}</span>
          <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
            <CardContent>
              <TextField
                id="standard-phone"
                label="手机号"
                value={this.state.phone}
                onChange={this.handleChange('phone')}
                className={classes.textField}
                type="text"
                margin="normal"
              />
              <TextField
                id="standard-auth"
                label="短信验证码"
                value={this.state.auth}
                onChange={this.handleChange('auth')}
                className={classes.textField}
                type="text"
                margin="normal"
              />
            </CardContent>
          </Collapse>
        </Card>
        <div className={classes.login_img1}>
          <img src={city} alt=""/>
        </div>
        <span className={classes.other_login}>社交账号登录</span>
        <div className={classes.other_icons}>
          <span className={classes.other_icon} ><img src={ali} alt=""/></span>
          <span className={classes.other_icon} ><img src={weichat} alt=""/></span>
        </div>
        <span className={classes.other_login}>还没有账号？<span className={classes.login}>
          <Link to="/reg">去注册</Link>
        </span></span>
      </div>
    )
  }
}

Login.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Login)
