import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card, CardMedia, CardActionArea, Typography, CardContent } from '@material-ui/core'

const styles = {
    card: {
        borderRadius: '40px'
    },
    image: {
        height: 210,
    },
}


const StoryCards = props => {
    const { classes } = props
    const { picture, author, info } = props.data
  return (
    <Card className={[`${classes.card}`, `card_shadow`].join(" ")}>
        <CardActionArea>
            <CardMedia
                className={classes.image}
                image={picture}
                title="Paella dish"/>
            <CardContent>
                <Typography>
                {author}
                </Typography>
                <Typography>
                {info}
                </Typography>
            </CardContent>
        </CardActionArea>
    </Card>
  )
}

StoryCards.propTypes = {
    classes: PropTypes.object.isRequired
  }
  
export default withStyles(styles)(StoryCards)
  
