import logo from '../assets/svg/Visual.svg'
import { Add } from '@material-ui/icons'
import { FilledInput, InputAdornment, FormControlLabel, Switch, FormLabel} from '@material-ui/core'

const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  button: {
    width: '100%',
    margin: '20px',
    height: '40px',
    borderRadius: '40px'
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: '100%'
  },
  title: {
    display: 'flex',
    flexFlow: 'column',
    alignItems: 'center',
    height: '100px',
    justifyContent: 'space-between',
    marginTop: '26px',
    fontSize: '14px',
  },
  addImage: {
    width: '60px',
    height: '60px',
    background: '#c5c5c5',
    color: '#fff',
    textAlign: 'center',
    lineHeight: '60px',
    fontSize: '34px',
    margin: '20px 10px',
  },
  init: {
    height: '60px',
    width: '100%',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginLeft: '20px',
    marginRight: '6px',
  },
})

const currencies = [
  {
    value: 'USD',
    label: '墨流设计',
  },
  {
    value: 'EUR',
    label: '软陶手工',
  },
  {
    value: 'BTC',
    label: '纸浆工艺',
  },
  {
    value: 'JPY',
    label: '光影设计',
  },
  {
    value: 'JPY1',
    label: '概念设计',
  },
  {
    value: 'JPY2',
    label: '写意画',
  }
];

class Release extends Component {
  state = {
    value: 'release',
    name: 'Cat in the Hat',
    age: '',
    multiline: 'Controlled',
    currency: 'EUR',
    hidden: false,
  }

  handleChange = name => event => {
    this.setState({ [name]: event.target.value })
  }

  handleHiddenChange = (event, hidden) => {
    this.setState(state => ({
      hidden,
      // hidden implies !open
      open: hidden ? false : state.open,
    }));
  };
  changHandler = e => {
    e.preventDefault()
    const file = e.target.files[0];
  }
  render() {
    const { classes } = this.props
    const { hidden } = this.state
    return (
      <div>
        <Header value={this.state.value}>
          发布创意
        </Header>
        <div className={classes.title}>
          <img style={{width: '60px'}} src={logo} alt=""/>
          <span>发布创意</span>
          <span>作品介绍中尽量描述创作故事</span>
        </div>
        <Wrapper box={true}>
          <form className={classes.container} noValidate autoComplete="off">
            <TextField
              id="standard-with-placeholder"
              label="名称"
              className={classes.textField}
              margin="normal"
            />
            <TextField
              id="standard-select-currency-native"
              select
              label="创意类别"
              className={classes.textField}
              value={this.state.currency}
              onChange={this.handleChange('currency')}
              SelectProps={{
                native: true,
                MenuProps: {
                  className: classes.menu,
                },
              }}
              helperText="请选择您要发布创意类别"
              margin="normal"
            >
              {currencies.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </TextField>
            <TextField
              id="standard-with-placeholder"
              label="价格"
              className={classes.textField}
              margin="normal"
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">¥</InputAdornment>
                ),
              }}
            />
            <TextField
              id="standard-with-placeholder"
              label="作品链接"
              className={classes.textField}
              margin="normal"
            />
            <TextField
              id="standard-with-placeholder"
              label="创意思路"
              className={classes.textField}
              margin="normal"
            />
            <TextField
              id="standard-with-placeholder"
              label="描述"
              className={classes.textField}
              multiline={true}
              rows={6}
              rowsMax={14}
            />
             <div className={classes.init}>
              <FormLabel component="legend">是否为原创</FormLabel>
              <FormControlLabel
                control={
                  <Switch
                    checked={hidden}
                    onChange={this.handleHiddenChange}
                    value="hidden"
                    color="primary"
                  />
                }
              />
            </div>
            <div style={{position: 'relative'}}>
              <div className={classes.addImage}>
                <Add />
              </div>
              <input type="file" style={
                {
                  position: 'absolute',
                  top: '40px',
                  opacity: 0,
                }
              }
              onChange={(e)=>this.changHandler(e)}/>
            </div>
            <Button variant="contained" color="primary" className={classes.button}>
              提交
            </Button>
          </form>
        </Wrapper>
        <div style={{height: 80}}></div>
        <BottomNav selected={this.state.value}/>
      </div>
    )
  }
}

Release.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Release)
