import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Card from '@material-ui/core/Card'
import TextField from '@material-ui/core/TextField'
import CardContent from '@material-ui/core/CardContent'
import InputAdornment from '@material-ui/core/InputAdornment'
import Visibility from '@material-ui/icons/Visibility'
import Collapse from '@material-ui/core/Collapse'
import VisibilityOff from '@material-ui/icons/VisibilityOff'
import Button from '@material-ui/core/Button'
import deepPurple from '@material-ui/core/colors/deepPurple'
import { Link } from "react-router-dom"
import city from '../assets/images/city.png'
import form from '../assets/images/form.png'
import Switch from '@material-ui/core/Switch'

const styles = theme => ({
  card: {
    margin: '16px',
    maxWidth: 400,
    boxShadow: '0 1px 10px #e6e4e4',
    borderRadius: 30
  },
  textField: {
    margin: '30px 16px',
    width: 300,
  },
  textField_sub:{
    width: '100%'
  }, 
  send: {
    textAlign: 'right',
    height: '32px',
    lineHeight: '32px',
    width: '80px',
    color: '#3f51b5',
    fontSize: '14px',
    padding: 0
  },
  button: {
    width: '90%',
    margin: '10px 16px',
    padding: '8px',
    color: theme.palette.getContrastText(deepPurple[500]),
    backgroundColor: '#3f51b5',
    borderRadius: 30,
    '&:hover': {
      backgroundColor: '#3f51b5',
    },
  },
  other_login: {
    display: 'flex',
    justifyContent: 'center',
    padding: '20px 0',
    color: '#969696',
    fontSize: 14
  },
  login: {
    color: '#969696'
  },
  login_img1: {
    position: 'fixed',
    bottom: 0,
    zIndex: -1
  },
  forgot: {
    width: '80px',
    display: 'flex',
    transform: 'translateX(228%)',
    color: '#3f51b5',
    padding: '12px 10px',
    transition: 'all .2s ease-in-out',
    fontSize: 14
  },
  retrievePassword: {
    width: '80px',
    color: '#3f51b5',
    display: 'flex',
    padding: '12px 10px',
    transform: 'translateX(50%)',
    marginLeft: '56px',
    transition: 'all .2s ease-in-out',
    fontSize: 14
  },
})

class Login extends Component {
  state = { 
    auth: '',
    phone: '',
    accout: '',
    password: '',
    showPassword: false,
    expanded: false,
    checked: false,
  }

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }))
  }

  handleChange = prop => event => {
    this.setState({ [prop]: event.target.value })
  }
  handleChangeSwitch = () => {
    this.setState(state => ({ checked: !state.checked }));
  };
  render() {
    const { classes } = this.props
    const { checked } = this.state
    return (
      <div>
       <Card className={classes.card}>
        <CardContent>
          <form noValidate autoComplete="off">
          <TextField
              id="standard-name"
              label="邮箱/手机号"
              value={this.state.accout}
              onChange={this.handleChange('accout')}
              className={classes.textField}
              type="text"
              margin="normal"
            />
            <TextField
              id="standard-password-input"
              label="密码"
              value={this.state.password}
              onChange={this.handleChange('password')}
              className={classes.textField}
              type={this.state.showPassword ? 'text' : 'password'}
              InputProps={{
                endAdornment: (
                    <InputAdornment position="end" onClick={this.handleClickShowPassword}>
                    {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                    </InputAdornment>
                ),
              }}
              margin="normal"
            />
            <TextField
                id="standard-auth"
                label="验证码"
                value={this.state.auth}
                onChange={this.handleChange('auth')}
                className={classes.textField}
                type="text"
                InputProps={{
                    endAdornment: (
                        <InputAdornment position="end">
                        <Button color="primary" className={classes.send}>
                            发送验证码
                        </Button>
                        </InputAdornment>
                    ),
                }}
                margin="normal"
            />
            <span className={this.state.expanded ? classes.retrievePassword : classes.forgot}
            onClick={this.handleExpandClick}
            aria-expanded={this.state.expanded}>{this.state.expanded ? '认证创造师' : '申请认证'}</span>
              <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
                <CardContent>
                  <TextField
                    id="standard-phone"
                    label="职业"
                    value={this.state.phone}
                    onChange={this.handleChange('phone')}
                    className={classes.textField_sub}
                    type="text"
                    margin="normal"
                  />
                  <TextField
                    id="standard-auth"
                    label="擅长的类别"
                    value={this.state.auth}
                    onChange={this.handleChange('auth')}
                    className={classes.textField_sub}
                    type="text"
                    margin="normal"
                  />
                  <TextField
                    id="standard-password-input"
                    label="是否原创为主"
                    value={this.state.password}
                    onChange={this.handleChange('isCreate')}
                    className={classes.textField_sub}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <Switch checked={checked} color="primary" onChange={this.handleChangeSwitch} aria-label="Collapse" />
                        </InputAdornment>
                      ),
                    }}
                    margin="normal"
                  />
                  <img src={form} alt=""/>
                </CardContent>
              </Collapse>
            <Button variant="contained" color="primary" className={classes.button}>
              注册
            </Button>
          </form>
        </CardContent>
        </Card>
        <div className={classes.login_img1}>
          <img src={city} alt=""/>
        </div>
       
        <span className={classes.other_login}>注册即同意 <span className={classes.login}>
        <Link to="/login">注册协议</Link>
        </span></span>
        <span className={classes.other_login}>已有账号？<span className={classes.login}>
        <Link to="/login">去登录</Link>
        </span></span>
      </div>
    )
  }
}

Login.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Login)
