import React, { Component } from 'react'
import { Typography } from '@material-ui/core'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import SwipeableViews from 'react-swipeable-views'
import { observer, inject } from 'mobx-react'
import Card from './Card'

const styles = {
  root: {
    width: '100%'
  },
  wrapper: {
    width: '57%',
    height: 230,
  },
  slider: {
    paddingLeft: '10px'
  },
  out: {
    width: '30px',
    marginTop: '4%',
    marginLeft: '4%',
    display: 'flex',
    height: '132px',
    borderLeft: '1px solid #c32c2c'
  },
  outline: {
    height: '132px',
    width: '42px'
  }
}

const SwipeableContent = ({children, dir}) => {
    return (
      <Typography component="div" dir={dir} >
        {children}
      </Typography>
    )
  }

SwipeableContent.propTypes = {
    children: PropTypes.node.isRequired,
    dir: PropTypes.string.isRequired,
}


@inject('homeStore')
@observer
class MidSlider extends Component {
    state = {
        value: 0
    }
    handleChangeIndex = index => {
      this.setState({ value: index })
    }
  render() {
    const { classes, theme } = this.props
    const { midSlider } = this.props.homeStore
    return (
        <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={this.state.value}
            onChangeIndex={this.handleChangeIndex}
            slideStyle={styles.wrapper}
            slideClassName={classes.slider} >
           {midSlider.map(item => {
              return (
                  <SwipeableContent dir={theme.direction} key={item._id}>
                    <Card data={item} />
                  </SwipeableContent>
                )
            })}
            {midSlider.length > 0 && <div className={classes.out}>
            <span className={classes.outline}></span>
            <span>我也是有底线的</span>
          </div>}
        </SwipeableViews>
    )
  }
}

MidSlider.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
  }
  
export default withStyles(styles, { withTheme: true })(MidSlider)
  
