import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card, CardHeader, CardMedia, CardContent, CardActions, Collapse, Avatar, IconButton, Typography } from '@material-ui/core'
import { Add, Remove } from '@material-ui/icons'
import FavoriteIcon from '@material-ui/icons/Favorite'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import classnames from 'classnames'
import { Link } from 'react-router-dom'
import Share from '../ShareComponents'
import { observer, inject } from 'mobx-react'

const styles = theme => ({
  SingleCard: {
    position: 'relative',
    background: '#ffd53e',
    borderTopLeftRadius: '40px',
    borderBottomLeftRadius: '40px',
    borderBottomRightRadius: '40px'
  },
  card: {
    marginTop: 14,
    borderTopRightRadius: '140px',
    borderTopLeftRadius: '40px',
    borderBottomLeftRadius: '40px',
    borderBottomRightRadius: '40px',
    minHeight: '430px'
  },
  add: {
    position: 'absolute',
    right: '16px',
    top: 0,
    width: '30px',
  },
  media: {
    borderRadius: '40px',
    paddingTop: '56.25%',
    margin: '0 10px'
  },
  cardText: {
    color: '#333e4a',
    textAlign: 'center',
    height: '60px',
    textOverflow: 'ellipsis',
    overflow: 'hidden',
    display: '-webkit-box',
    lineClamp: '3',
    boxOrient: 'vertical'
  },
  actions: {
    display: 'flex',
    padding: '0 10px'
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  fav: {
    color: 'rgba(0, 0, 0, 0.54)'
  },
  favOpend: {
    color: '#ff4c4c'
  },
  avatar: {
    width: '50px',
    height: '50px'
  }
})
const Person = props => <Link to="/person" {...props} />

@inject('homeStore')
@observer
class SingleCard extends Component {

  state = {
    expanded: false,
    fav: false,
    attentionStatus: false
  }

  handleExpandClick = () => this.setState(state => ({ expanded: !state.expanded }))
  handleFavClick = () => this.setState(state => ({ fav: !state.fav }))
  attentionHandleClick = () => this.setState(state => ({attentionStatus: !state.attentionStatus}))
  
  render() {
    const { classes } = this.props
    const { attentionStatus } = this.state
    const { recommended } = this.props.homeStore
    return (
      <div className={classes.SingleCard}>
          <span onClick={this.attentionHandleClick} className={classes.add}>
            <IconButton aria-label="attention" style={{color: '#fff'}}>
            { attentionStatus ? <Remove /> : <Add /> }
            </IconButton>
          </span>
        {
          recommended.length > 0 
          &&
          recommended.map(item=>{
            return (
                <Card key="1" className={[`${classes.card}`, `card_shadow`].join(" ")}>
                  <CardHeader
                    avatar={
                      <Avatar aria-label="Recipe" className={classes.avatar}>
                        <img src={item.avatar} alt=""/>
                      </Avatar>}
                    title={item.author}
                    subheader={item.info}
                    component={Person} />
                  <CardMedia
                    className={classes.media}
                    image={item.picture}
                    title="Paella dish" />
                  <CardContent>
                    <Typography component="p">{item.bio}</Typography>
                  </CardContent>
                  <CardActions className={classes.actions} disableActionSpacing>
                    <IconButton aria-label="Add to favorites" className={classnames(classes.fav, {
                        [classes.favOpend]: this.state.fav,
                      })} onClick={this.handleFavClick}>
                      <FavoriteIcon />
                    </IconButton>
                    <Share />
                    <IconButton
                      className={classnames(classes.expand, {[classes.expandOpen]: this.state.expanded})}
                      onClick={this.handleExpandClick}
                      aria-expanded={this.state.expanded}
                      aria-label="Show more">
                      <ExpandMoreIcon />
                    </IconButton>
                  </CardActions>
                  <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
                    <CardContent>
                      <Typography paragraph>{item.p1}</Typography>
                      <Typography paragraph>{item.p2}</Typography>
                      <Typography style={{paddingRight: '30px', textAlign: 'right'}}>
                        <a style={{color: '#3f51b5'}} href={item.link}>查看更多关于她的作品</a>
                      </Typography>
                    </CardContent>
                  </Collapse>
                </Card>
            )
          })
        }
      </div>
    )
  }
}

SingleCard.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(SingleCard)
