import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card } from '@material-ui/core'
import { Layout, TabsType, BottomNav, MidSlider, Header } from '../components'
import { observer, inject } from 'mobx-react'

const styles = {
  card: {
    borderRadius: 0,
    boxShadow: 'none',
    background: 'linear-gradient(#f9f9f9, white)'
  },
  button: {
    width: '100%',
    marginTop: '16px',
    height: '42px'
  },
}

@inject('homeStore')
@observer
class Inspiration extends Component {
  state = {
    name: 'inspiration',
  }

  componentDidMount() {
    this.fetchList()
  }
  fetchList(){
		this.props.homeStore.getSilderList()
  }

  render() {
    const { classes } = this.props
    return (
      <div>
        <Header>
          相遇灵感
        </Header>
        
        <Card className={classes.card}>
          <MidSlider />
        </Card>

        <TabsType />
        
        <BottomNav selected={this.state.name}/>
        <div style={{height: 120}}></div>
      </div>
    )
  }
}
Inspiration.propTypes = {classes: PropTypes.object.isRequired}

export default withStyles(styles)(Inspiration)

