import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
import SearchIcon from '@material-ui/icons/Search'
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import { Menu, MenuItem } from '@material-ui/core'
import classnames from 'classnames'
import TextField from '@material-ui/core/TextField'
import Fade from '@material-ui/core/Fade'
import Zoom from '@material-ui/core/Zoom'
import logout from '../../assets/svg/logout.svg'
import logo from '../../assets/images/logo.png'
import lightbulb from '../../assets/svg/ic_lightbulb_outline.svg'
import devices from '../../assets/svg/ic_important_devices.svg'
import paint from '../../assets/svg/ic_format_paint.svg'
import { MoreVert } from '@material-ui/icons'

const styles = {
  root: {
    height: '42px',
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    borderRadius: '30px',
    boxShadow: '0 5px 10px #eae7ef',
    margin: '12px 10px 16px',
    transition: 'border-radius.3s ease-in-out, background .3s ease-in-out'
  },
  input: {
    marginLeft: 8,
    flex: 1,
  },
  iconButton: {
    padding: 10,
    color: '#584971'
  },
  iconButtonDark: {
    padding: 10,
    color: '#fff'
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4,
  },
  showTitle: {
    transform: 'scale(1,1)',
    transition: 'all .3s ease-in-out'
  },
  hideTitle: {
    transform: 'scale(0,0)',
    transition: 'all .3s ease-in-out'
  },
  "@keyframes showAnimation": {
    from: {width: 0},
    to: {width: '100%'},
  },
  textField: {
    animation: 'showAnimation .3s ease-in-out',
    width: '100%'
  },
  side: {
    width: '226px'
  },
  header: {
    position: 'fixed',
    top: '50%',
    width: '667px',
    height: '667px',
    background: '#dc4545',
    borderRadius: '30%',
    marginLeft:' -558px',
    marginTop: '-344px'
  },
  list: {
    width: '220px',
    marginTop: '260px',
    marginLeft: '50px'
  },
  icons: {
    color: '#fff',
    width: '26px'
  },
  logo: {
    position: 'absolute',
    right: 0,
    top: 0
  },
  logout: {
    position: 'absolute',
    bottom: '12px',
    width: '20px',
    right: '23px',
  },
  fiexd: {
    position: 'fixed',
    top: 0,
    width: '100%',
    zIndex: 99,
    margin: 0,
    borderRadius: 0,
    boxShadow: 'none',
    color: '#fff',
    height: '50px',
    background: '#3f50b5',
    transition: 'border-radius.3s ease-in-out, background .3s ease-in-out',
    boxShadow: '0 5px 10px #313131'
  }
}

const iOS = process.browser && /iPad|iPhone|iPod/.test(navigator.userAgent)

class Header extends Component {
    state = {
        left: false,
        hide: false,
        showInput: false,
        fixed: false,
        anchorEl: null,
      }


      handleClick = event => {
        this.setState({ anchorEl: event.currentTarget });
      };
    
      handleClose = () => {
        this.setState({ anchorEl: null });
      };

      componentWillMount(){
        window.addEventListener('scroll', this.handleScroll)
      }
    
      componentWillUnmount(){
          window.removeEventListener('scroll',this.handleScroll)
      }
    
      handleScroll = () => {
        let lastScrollY = window.scrollY
    
        // console.log(lastScrollY)
        this.setState({
          fixed: (lastScrollY > 50) ? true : false
        })
      }
    
      toggleDrawer = (side, open) => () => {
        this.setState({
          [side]: open,
        })
      }

      changeTitleHandler = () => {
        this.setState({
          hide: !this.state.hide
        })
        setTimeout(()=>{
          this.setState({
            showInput: !this.state.showInput
          })
        },200)
      }
    
    render(){
    const { classes } = this.props
    const { hide, showInput, fixed, anchorEl } = this.state
    const sideList = (
      <div className={classes.list}>
        <div>
          <Zoom in={true}>
            <div className={classes.header}></div>
          </Zoom>
          <Zoom in={true}>
            <span className={classes.logo}>
            </span>
          </Zoom>
          <span className={classes.logout}><img src={logout} alt="" /></span>
        </div>
        <List className={classes.list}>
          {[
            {
              name: '在售作品',
              link: devices
            },{
              name: '灵感采集',
              link: lightbulb
            },{
              name: '工具市场',
              link: paint
            }
          ].map((item, index) => (
            <Zoom in={true} style={{ transitionDelay: `${index}`+ `10ms`}} key={index}>
              <ListItem button >
                <ListItemIcon className={classes.icons}>{
                  <img src={item.link} alt="" />
                }</ListItemIcon>
                <ListItemText primary={item.name} />
              </ListItem>
            </Zoom>
          ))}
        </List>
      </div>
      )

      return (
        <div>
          <Paper className={classnames(classes.root, {
          [classes.fiexd]: fixed,
        })} elevation={1}>
          <IconButton onClick={this.toggleDrawer('left', true)} className={classnames(classes.iconButton, {
          [classes.iconButtonDark]: fixed,
        })} aria-label="Menu">
              <MenuIcon />
          </IconButton>
            {
              showInput 
              ?
              <TextField
                id="standard-name"
                className={classes.textField}
                // value={this.state.name}
                // onChange={this.handleChange('name')}
                margin="normal"
              />
              :
              <Fade in={!hide}>
                <span className={classnames(classes.showTitle, {
                  [classes.hideTitle]: hide,
                })}>{this.props.children}</span>
              </Fade>
            }
          {
            this.props.value === 'release' 
            ? <IconButton className={classnames(classes.iconButton, {
              [classes.iconButtonDark]: fixed,
              })} onClick={this.handleClick}><MoreVert /></IconButton>
            :
          <IconButton className={classnames(classes.iconButton, {
          [classes.iconButtonDark]: fixed,
          })} aria-label="Search" onClick={this.changeTitleHandler}>
              <SearchIcon />
          </IconButton>
          }
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={this.handleClose}
          >
            <MenuItem onClick={this.handleClose}>发布创意</MenuItem>
            <MenuItem onClick={this.handleClose}>散发灵感</MenuItem>
            <MenuItem onClick={this.handleClose}>分享采集</MenuItem>
          </Menu>
          </Paper>
          <SwipeableDrawer
            disableBackdropTransition={!iOS} disableDiscovery={iOS}
            open={this.state.left}
            onClose={this.toggleDrawer('left', false)}
            onOpen={this.toggleDrawer('left', true)}
            className={classes.side}>
            <div
              tabIndex={0}
              role="button"
              onClick={this.toggleDrawer('left', false)}
              onKeyDown={this.toggleDrawer('left', false)}>
               {sideList}
            </div>
          </SwipeableDrawer>
        </div>
      )
    }
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Header)