import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import {List,ListItem,ListItemText,ListItemAvatar,ListItemSecondaryAction} from '@material-ui/core'
import Avatar from '@material-ui/core/Avatar'
import {Typography, Card} from '@material-ui/core'
import { observer, inject } from 'mobx-react'
import IconButton from '@material-ui/core/IconButton';
import CommentIcon from '@material-ui/icons/Comment';

const styles = theme => ({
  card: {
    borderTopLeftRadius: '40px',
    borderBottomLeftRadius: '40px',
    margin: '10px 0',
    height: '68px'
  },
  root: {
    width: '100%',
  },
  inline: {
    display: 'inline',
  },
})

@inject('homeStore')
@observer
class Teacher extends Component {
  render(){
  const { classes } = this.props
  const { tearch } = this.props.homeStore
  return (
    <List className={classes.root}>
      {
        tearch.map(item=>
          <Card className={[`${classes.card}`, `card_shadow`].join(" ")} key={item.id}>
            <ListItem alignItems="flex-start">
              <ListItemAvatar>
                <Avatar alt="Remy Sharp" src={item.avatar} />
              </ListItemAvatar>
              <ListItemText
                primary={item.name}
                secondary={
                  <React.Fragment>
                    <Typography component="span" className={classes.inline} color="textPrimary">
                      {item.bio}
                    </Typography>
                  </React.Fragment>
                }
              />
              <ListItemSecondaryAction>
                <IconButton aria-label="Comments" style={{color: '#ffd540'}}>
                  <CommentIcon />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          </Card>
        )
      }
    </List>
  )
}}

Teacher.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Teacher)