import React, { Component } from 'react'
import { Card, CardMedia, CardActionArea, IconButton } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import FavoriteIcon from '@material-ui/icons/Favorite'
import classnames from 'classnames'

const styles = {
  cards: {
    position: 'relative',
    width: 210,
    marginRight: 16
  },
  card: {
    height: 150,
    width: 210,
    marginBottom: 10,
    borderRadius: '26px'
  },
  media: {
    height: 150,
  },
  info: {
    marginLeft: '16px'
  },
  fav: {
    position: 'absolute',
    right: 0,
    top: 0,
    padding: 0,
    zIndex: 99,
    width: '48px',
    height: '48px',
  },
  favOpend: {
    color: '#ff4c4c',
  },
}

class Mid_card extends Component {

  state = {
    fav: false,
  }

  handleFavClick = () => this.setState(state => ({ fav: !state.fav }))

  render(){
    const { classes } = this.props
    const { picture, author, info } = this.props.data
    return (
      <div className={classes.cards}>
        <Card className={[`${classes.card}`, `card_shadow`].join(' ')} >
          <IconButton aria-label="favorites" className={classnames(classes.fav, {
              [classes.favOpend]: this.state.fav,
            })} onClick={this.handleFavClick}>
            <FavoriteIcon />
          </IconButton>
        <CardActionArea>
          <CardMedia 
            className={classes.media}
            image={picture}
            title="author" />
        </CardActionArea>
        </Card>
        <div className={classes.info}>
          <Typography variant="subtitle1">
            {author}
          </Typography>
          <Typography variant="caption">
            {info}
          </Typography>
        </div>
        
      </div>
    )
}}

Mid_card.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Mid_card)
