import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card, CardActionArea, CardContent, CardMedia } from '@material-ui/core'
import { Link } from 'react-router-dom'

const styles = {
    card: {
        width: '120px',
        display: 'inherit',
        borderRadius: '26px',
        position: 'relative'
    },
    media: {
      height: 147,
    },
    info: {
        display: 'flex',
        flexFlow: 'column',
        justifyContent: 'space-evenly',
        height: '48px',
        padding: '6px 16px',
        fontSize: '12px'
    }
  }

const Slide_Card = props => {
    const { classes } = props
    const { picture, author, info } = props.data
    return (
        <Card className={[`${classes.card}`, `card_shadow`].join(' ')}>
            <CardActionArea component={Link} to="/worksDetail">
                <CardMedia
                className={classes.media}
                image={picture}
                title={author} />
                <CardContent className={classes.info}>
                    <span>{author}</span>
                    <span>{info}</span>
                </CardContent>
            </CardActionArea>
        </Card>
    )
}

Slide_Card.propTypes = {
    classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Slide_Card)